* {
    box-sizing: border-box; }

h1, h3, p {
    margin: 0; }

.input-settings {
    font-size: 16px;
    line-height: 24px;
    border: 1px solid #d9d9d9;
    border-radius: 2px;
    padding-left: 32px;
    color: #877777;
    &::placeholder {
        color: #1a1a1a; } }


body {
    background: linear-gradient(180deg, rgba(245, 245, 245, 0) 1.04%, #f5f5f5 100%);
    font-family: 'Roboto', sans-serif;
    font-display: swap;
    font-style: normal;
    font-weight: normal;
    padding: 0;
    margin: 0; }

.container {
    max-width: 1220px;
    padding: 0 10px;
    margin: auto; }

.header {
    margin-top: 45px;
    margin-bottom: 55px;

    .navigation {
        display: flex;
        justify-content: space-between;
        margin-bottom: 45px;

        .address-input {
            width: 690px;
            height: 40px;
            margin-right: 10px;
            background: url(../../img/home.svg) no-repeat left 10px top 10px; }

        .buttons {
            display: flex;

            button:hover {
                    box-shadow: 2px 6px 15px rgba(0, 0, 0, 0.3);
                    transition: all 0.2s;
                    opacity: 0.9;
                    transform: scale(1.1);
                    cursor: pointer; }

            .nav-button {
                max-height: 40px;
                background-repeat: no-repeat;
                background-position: left 16px center;
                padding: 8px 16px 8px 33px;
                font-size: 16px;
                border-radius: 3px;
                line-height: 24px; }

            .login-btn {
                background-color: #1890FF;
                border: 1px solid #1890ff;
                background-image: url(../../img/user.svg);
                margin-right: 10px;
                color: #fff;
                transition: all 0.2s; }

            .basket-btn {
                background-color: #fff;
                border: 1px solid #ececec;
                background-image: url(../../img/basket.svg);
                color: #595959;
                transition: all 0.2s; } } }

    .billboard {
        height: 300px;
        box-shadow: 0px 7px 12px rgba(158, 158, 163, 0.1);
        background-color: #FFF1B8;
        background-image: url(../../img/bg-billboard.png);
        background-repeat: no-repeat;
        background-position: right center;

        .content {
            padding: 68px 0 0 70px;

            .title {
                margin: 0;
                max-width: 405px;
                font-weight: bold;
                font-size: 39px;
                line-height: 46px; }

            .description {
                color: #302C34;
                max-width: 520px;
                font-size: 24px;
                line-height: 28px; } } } }

.products {

        .restaurants {}

        .caption {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 50px;

            .caption-text {
                color: #000;
                font-weight: bold;
                font-size: 36px;
                line-height: 42px; }

            .caption-search-input {
                width: 305px;
                height: 35px;
                background: url(../../img/search.svg) no-repeat left 12px center; } }

        .dishes {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;

            .block {
                overflow: hidden;
                flex-basis: 384px;
                height: 379px;
                background-color: #fff;
                box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
                border-radius: 7px;
                margin-bottom: 30px;
                transition: all 0.2s;

                &:hover {
                    transform: scale(1.05);
                    transition: all 0.2s;
                    cursor: pointer; }

                img {
                    object-fit: cover;
                    width: 384px;
                    height: 250px; }

                .description {
                    margin: auto;
                    width: 336px;

                    .description-title {
                        padding-top: 20px;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        margin-bottom: 12px;

                        p {
                            font-weight: bold;
                            font-size: 24px;
                            line-height: 32px; }

                        span {
                            font-style: normal;
                            font-size: 12px;
                            line-height: 20px;
                            color: #FFF;
                            background-color: #262626;
                            border-radius: 3px;
                            padding: 2px 8px; } }

                    .description-info {

                        span {
                            font-weight: normal;
                            font-size: 18px;
                            line-height: 32px;
                            color: #8c8c8c;

                            &.rub {
                                font-family: 'PT Sans', serif;
                                font-weight: normal;
                                font-style: normal;
                                font-size: inherit; }

                            &.rate {
                                background: url(../../img/rate-icon.svg) no-repeat left 0px top 3px;
                                padding-left: 22px;
                                padding-right: 20px;
                                color: #FFC107; }

                            &.price::after {
                                content: '';
                                margin: 0 10px 1% 10px;
                                background-color: #8c8c8c;
                                display: inline-block;
                                width: 5px;
                                height: 5px;
                                border-radius: 50%; } } } } } } }

footer {
    background-color: #fff;

    .footer-content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 60px 0 60px;

        .footer-nav {

            ul {
                display: flex;

                li {
                    list-style: none;
                    padding-right: 15px;

                    a {
                        text-decoration: none;
                        font-weight: normal;
                        font-size: 18px;
                        line-height: 21px;
                        color: #595959;
                        transition: all 0.2s;

                        &:hover {
                            color: #1890ff;
                            transition: all 0.2s;
                            cursor: pointer; } } } } }

        .footer-socials {

            a {
                padding-left: 20px; } } } }

@media only screen and (max-width: 480px) {

    .container {
        max-width: 480px;
        width: 300px; }

    .header {

        .navigation {
            flex-direction: column;
            align-items: center;
            justify-content: center;

            .logo {
                padding-bottom: 10px; }

            .address-input {
                width: 220px;
                margin-bottom: 5px;
                margin-right: 0; } }

        .billboard {
            width: auto;
            height: auto;
            background: none;
            box-shadow: none;

            .content {
                padding: 0;

                .title {
                    text-align: center;
                    margin-bottom: 15px; }

                .description {
                    text-align: center; } } } }

    .restaurants {
        .caption {
            flex-direction: column;
            margin-bottom: 25px;

            .caption-text {
                margin-bottom: 10px; }

            .caption-search-input {
                width: 240px; } }

        .block {

            img {
                max-width: 315px; }

            .description {
                max-width: 267px; } } }

    .footer-content {
        flex-direction: column;
        justify-content: center;

        .footer-nav {
            margin-top: 10px;

            ul {
                flex-wrap: wrap;
                align-items: center;
                justify-content: space-around;

                li {
                    padding-bottom: 27px;

                    a {

                        padding: 2px 5px;
                        border-radius: 2px;
                        border: 1px solid #d8d8d8; } } } } } }

